<script>
	import Card from '$lib/componant/card.svelte';
    import { createSearchStore, searchHandler } from '$lib/stores/search';
	export let data;
    import { onDestroy } from 'svelte';
	let channels = data.result;
	const genreMap = {
		8: 'Sports',
		5: 'Entertainment',
		6: 'Movies',
		12: 'News',
		13: 'Music',
		7: 'Kids',
		9: 'Lifestyle',
		10: 'Infotainment',
		15: 'Devotional',
		0x10: 'Business',
		17: 'Educational',
		18: 'Shopping',
		19: 'JioDarshan'
	};
	const lenMap = {
		6: 'English',
		1: 'Hindi',
		2: 'Marathi',
		3: 'Punjabi',
		4: 'Urdu',
		5: 'Bengali',
		7: 'Malayalam',
		8: 'Tamil',
		9: 'Gujarati',
		10: 'Odia',
		11: 'Telugu',
		12: 'Bhojpuri',
		13: 'Kannada',
		14: 'Assamese',
		15: 'Nepali',
		16: 'French'
	};
	let channelsData = [];
	let timeout;
    let query = '';
// document.getElementById("dsc").values
    const searchProducts = data.result.map((e) => ({
		...e,
		searchTerms: `${e.channelCategoryId} ${e.channel_id} ${e.channel_name} ${genreMap[e.channelCategoryId]} ${lenMap[e.channelLanguageId]}`
	}));
    const searchStore = createSearchStore(searchProducts);
    const unsubscribe = searchStore.subscribe((model) => searchHandler(model));
	function search(key) {
		clearTimeout(timeout);
		timeout = setTimeout(() => {
			channelsData = channels.map((e) =>
			`${e.channelCategoryId} ${e.channel_id} ${e.channel_name} ${genreMap[e.channelCategoryId]} ${
				lenMap[e.channelLanguageId]
			}`.includes(query)
		);
		}, 500);
	}
</script>

<div class="container">
	<div class="mb-2 ml-1">
	<!-- <div class="flex flex-row m-2 p-5 gap-3 justify-center align-middle"> -->
            <input
			type="search"
			id="search"
            bind:value={$searchStore.search}
            on:keyup={search}
			class="relative w-[30%] col-span-4 block mx-4 rounded-l border border-solid border-neutral-300 bg-transparent bg-clip-padding px-3 py-1.5 text-base font-normal text-neutral-700 outline-none transition duration-300 ease-in-out focus:border-primary focus:text-neutral-700 focus:shadow-te-primary focus:outline-none dark:text-neutral-200 dark:placeholder:text-neutral-700"
			placeholder="Search"
            />
            <!-- <label class="" for="search"><svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 50 50" width="26px" height="26px"><path d="M 21 3 C 11.601563 3 4 10.601563 4 20 C 4 29.398438 11.601563 37 21 37 C 24.355469 37 27.460938 36.015625 30.09375 34.34375 L 42.375 46.625 L 46.625 42.375 L 34.5 30.28125 C 36.679688 27.421875 38 23.878906 38 20 C 38 10.601563 30.398438 3 21 3 Z M 21 7 C 28.199219 7 34 12.800781 34 20 C 34 27.199219 28.199219 33 21 33 C 13.800781 33 8 27.199219 8 20 C 8 12.800781 13.800781 7 21 7 Z"/></svg></label> -->
        
	</div>
	<div class="container grid place-items-center grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-6 2xl:grid-cols-8 mx-auto">
		{#each $searchStore.filtered as channel}
			<Card {channel} />
		{/each}
	</div>
</div>
